.filter-checkbox label:after,
.filter-radio label:after {
  content: '';
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 2px;
  border: 2px solid #dd003f;
  border: 2px solid var(--ui-color-1);
  margin-left: auto;
  margin-right: 40%;
  padding: 2px;
  background-clip: content-box; /* support: IE9+ */
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.filter-checkbox input:checked ~ label:after,
.filter-radio input:checked ~ label:after {
  background-color: #dd003f;
  background-color: var(--ui-color-1);
}

.filter-checkbox input:focus ~ label:after,
.filter-radio input:focus ~ label:after {
  outline: 1px solid #dcf836;
