/* Preloading Homepage Hero Image */

@supports ((-webkit-filter: blur(1px)) or (filter: blur(1px))) {
  .slider {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
  }
  .slider-bg::after {
    -webkit-animation: sharpen 0.7s both;
            animation: sharpen 0.7s both;
  }
  @-webkit-keyframes sharpen {
    from {
      background-image: url(/film-app/static/media/main-bg.129a3b03.jpg);
      -webkit-filter: blur(20px);
              filter: blur(20px);
    }
    to {
      background-image: url(/film-app/static/media/main-bg.129a3b03.jpg);
      -webkit-filter: blur(0px);
              filter: blur(0px);
    }
  }
  @keyframes sharpen {
    from {
      background-image: url(/film-app/static/media/main-bg.129a3b03.jpg);
      -webkit-filter: blur(20px);
              filter: blur(20px);
    }
    to {
      background-image: url(/film-app/static/media/main-bg.129a3b03.jpg);
      -webkit-filter: blur(0px);
              filter: blur(0px);
    }
  }
}

