  --par-6-position: 
	 	0 calc(5 * var(--par-margin)),
  	0 calc((5 * var(--par-margin)) + (1 * 25px)),
  	0 calc((5 * var(--par-margin)) + (2 * 25px)),
  	0 calc((5 * var(--par-margin)) + (3 * 25px));

  --blur-size: 300px 100%;

	height: 696px;
  background-repeat: no-repeat;
  background-image: 
  	-webkit-linear-gradient(left, #020d1805 0, #020d18cc 50%, #020d1805 100%),
