.modal {
  opacity: 0;
  display: block;
  height: 100%;
  width: 100%;
  -webkit-transition: all 0.35s ease-out;
  transition: all 0.35s ease-out;
  background-color: rgba(0, 0, 0, 0.75);
}

.modal--open {
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 1;
}

.modal__dialog {
	width: 430px;
	margin: 5% auto;
}

.modal__dialog--large {
	width: 750px;
}

.modal__content {
  position: relative;
	padding: 50px 35px;
  border: 1px solid #e1e1e1;
  background-color: #ffffff;
}

.modal__heading {
  font-family: 'Dosis', sans-serif;
  font-size: 36px;
  color: #222222;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 45px;
  text-align: center;
}

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  background-color: var(--ui-color-1);
}

.modal__close svg {
  width: 50%;
  height: 100%;
}

.modal__close:hover path,
.modal__close:focus path {
  fill: var(--ui-color);
}

.modal__btn {
  width: 100%;
	display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Dosis', sans-serif;
  font-size: 14px;
  padding: 2px 10px;
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
  border: none;
  background-color: var(--ui-color-1);
  height: 42px;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border: 2px solid var(--ui-color-1);
}

.modal__btn--inline {
  display: inline-flex;
  line-height: 1;
  width: auto;
}


.modal__btn:hover,
.modal__btn:focus {
  color: #000;
  background-color: #fff;
}

.modal__btn[disabled], .modal__btn.disabled {
  background-color: #fff;
  color: #000;
  border-color: grey;
  cursor: auto;
  color: grey;
  opacity: 0.6;
}

.modal__btn--white {
  background-color: #fff; 
  color: #000;
  border: 2px solid var(--ui-color-1);
}

.modal__btn--white:hover,
.modal__btn--white:focus {
  color: var(--ui-color-1);
  background-color: #fff;
}

