/* Checkbox */

.login-modal__remember {
	display: inline-flex;
}

.login-modal__remember label {
  display: flex;
  align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Nunito', sans-serif;
  font-size: #abb7c4;
  color: #222222;
  font-weight: 600;
  text-transform: none;
}

.login-modal__remember label:before {
  content: '';
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 2px;
  margin-right: 5px;
  border: 2px solid var(--ui-color-1);
  padding: 2px;
  background-clip: content-box; /* support: IE9+ */
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.login-modal__remember input:checked ~ label:before {
  background-color: var(--ui-color-1);
}

.login-modal__remember input:focus ~ label:before {
